.commpage {
    width: 100%;
    overflow: hidden;

    ::v-deep .el-card__header {
        padding: 0.16rem 0.13rem 0.16rem 0.4rem;
    }

    ::v-deep .el-card__header {
        position: relative;
    }

    ::v-deep .el-card__header::after {
        content: "";
        position: absolute;
        background: #2e4af7;
        left: 0.24rem;
        top: 0.18rem;
        width: 6px;
        height: 0.2rem;
    }

    .clearfix {
        color: #333333;
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 0.16rem;
    }

    ::v-deep .el-card__body {
        padding: 0;
    }


}

.el-form--inline {

    .min-item {
        margin-right: 10px !important;
        width: 25%;
    }

    .small-item {
        margin-right: 175px !important;
    }

    .max-item {
        width: 35%;
        margin-right: 10px !important;

        ::v-deep .el-form-item__content {
            .el-date-editor .el-range-input {
                width: 42%;
            }
        }
    }

    .min-item,
    .max-item {
        ::v-deep .el-form-item__label {
            text-align: left;
        }
    }

}

.small-size {
    font-size: 0.14rem;
}

.mid-size {
    font-size: 0.16rem;
}

.big-size {
    font-size: 0.23rem;
}

.pagination-box {
    padding-top: 20px;
    text-align: right;

    .el-pager li {
        border-radius: 4px;
        border: 1px solid #D9D9D9;
        min-width: 28px !important;
        color: rgba(0, 0, 0, 0.65);
        // width: 28px;
        height: 28px;
        line-height: 28px;
        margin: 0 4px;
    }

    .el-pagination button {
        min-width: 28px;
        border: 1px solid #D9D9D9;
        border-radius: 4px;
        width: 28px;
        height: 28px;
    }

    .el-pagination .btn-next {
        padding-left: 6px;
    }
}

.el-dialog .el-dialog__body {
    // border-top: 1px solid #dcdfe6;
    // border-bottom: 1px solid #dcdfe6;
    max-height: calc(100vh - 181px);
    overflow-y: auto;
    overflow-x: hidden;
}